.stage {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 110%;
	background: linear-gradient(-45deg, darkslateblue, #2787f5, darkslateblue, darkslateblue);
  background-size: 400% 400%;
	animation: gradient 15s ease infinite;
  }
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}



  
.stageSucces {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden ;
	background: linear-gradient(-45deg, darkslateblue, skyblue, darkslateblue, darkslateblue);
  background-size: 400% 400%;
	animation: gradient 5s ease infinite;
  }
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.stageError {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden ;
	background: linear-gradient(-45deg, darkslateblue, skyblue, darkslateblue, darkslateblue);
  background-size: 400% 400%;
	animation: gradient 15s ease infinite;
  }
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

